a{
    text-decoration: none;
    color: inherit;
}
.container{
    max-width: 1080px;
    padding: 0 1rem;
    margin:  0 auto;
}
.login-area{
    height: 90vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loginMain{
    gap: 1rem;
    margin-top: 2rem;
    grid-template-columns: 1fr 1fr;
    display: grid;
    width: 100%;
    background-color: var(--color-white);
    padding: var(--card-padding);
    border-radius: var(--card-border-radius);
}
.loginLeft{
    padding: 3rem ;
}
.loginLeft .logo{
    margin-bottom: var(--card-padding);
}
.loginLeft .description{
    font-size: var(--small-font-size);
}
.login-logo-text{
    color: var(--color-primary);
    font-size: var(--normal-font-size);
    /* text-decoration: underline; */
    cursor: pointer;
}
.input_area{
    margin: 1rem 0;
}
.input_area .input_box{
    width: 100%;
    display: flex;
    flex-direction:  column;
    gap: .5rem  ;
    margin: 1rem 0;
}
.input_box .label{
    font-size: var(--normal-font-size);
}
.login-input{
    width: 100%;
    height: 2.5rem;
    padding: 0 1rem;
    border-radius: var(--card-border-radius);
    font-size: var(--normal-font-size);
    border: 1px solid var(--color-secondary);
}
.input:focus{
    border: 1px solid var(--color-primary);
}
.required{
    color: red;
}
.login-links{
    width: 100%;
    display: flex;
    margin-top: 1.5rem;
}
.forgot-link{
    color: var(--color-primary);
    text-align: right;
    margin-left: auto;
    font-size: var(--small-font-size);
}
.button-container{
    margin: 1rem 0;
    /* padding: 0; */
    display: flex;
    /* flex-direction: column; */
    gap: 10px;
}
.btn-large{
    width: 100%;
    background-color: var(--color-primary);
    color: var(--color-white);
    height: 2.5rem;
    font-size: var(--normal-font-size);
}
.btn-2{
    width: 100%;
    /* background-color: var(); */
    border: 1px solid var(--color-secondary);
    color: var(--color-black);
    background-color: var(--color-white);
    height: 2.5rem;
    font-size: var(--normal-font-size);
}

.or-text-box{
    display: flex;
    align-items: center;
    justify-content: center;
}
.line{
    height: 1px;
    background-color: rgb(231, 217, 217);
    width:100%;
    margin: 0 20px;
}
.or-text{
    color: var(--color-gray);
}
.social-container{
    width: 100%;
    margin: 1.5rem 0;
}
.social-button{
    background-color: transparent;
    font-size: var(--normal-font-size);
    border: 1px solid var(--color-secondary);
    width: 100%;
    height: 3rem;
    color: var(--color-black);
}
.social-button:hover{
    background-color: var(--color-secondary);
}
.password-eye{
    position: relative;
}
.eye{
    position:absolute;
    right: 1rem;
    top: 2px;
    cursor: pointer;
    opacity: .8;
    color: var(--color-primary);
}
/* Login Right Side  */
.loginRight{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: var(--card-padding) ;
}
.login_img{
    height: 500px;
    width: 400px;
    border-radius: var(--card-padding);
    aspect-ratio: auto;
}


.register-area{
    display: grid;
    grid-template-columns: 1fr 1fr;
    /* grid-template-rows: 2fr 1fr 1fr 1fr; */
    gap: 1rem;
}
.register-area .input_box{
    margin: 0;
}


@media screen and (max-width:768px) {
    .loginMain{
        grid-template-columns: 1fr;
    }
    .loginRight{
        display: none;
    }
    .loginLeft{
        padding: 10px;
    }
}