.sidebar {
    /* flex: 1; */
    height: calc(100vh - 50px);
    background-color: rgb(251, 251, 255);
    position: sticky;
    top: 50px;
    left: 0;
  }

  .sidebarWrapper {
    border-top: 1px solid rgb(226, 207, 207);
    width: 300px;
    color: #555;
    padding: 10px;
    margin: 0 0  20px 0 ;
    margin-top: 0;
  }
  label{
    color: #222;
  }
  .sidebarMenu {
    margin-bottom: 10px;
  }

  .sidebarTitle {
    font-size: 13px;
    color: rgb(187, 186, 186);
  }

  .sidebarList {
    list-style: none;
    padding: 5px;
  }

  .sidebarListItem {
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
  }

  .sidebarListItem.active,
  .sidebarListItem:hover {
    background-color: rgb(240, 240, 255);
  }

  .sidebarIcon{
      margin-right: 5px;
      font-size: 20px !important;
  }